	module.exports = {"data":{"post":{"html":"<p>Some things are better left to professionals. Right now I wish potty training could be one of them.</p>\n<p>In the presence of a professional child care provider, Christopher is able to go without a diaper the entire day. Then his amatuer child care father takes him home. Despite denying repeatly that he needs to use the toilet, Christopher walks in the door, pulls down his pants, marches into the living room and proceeds to piss on his play carpet.</p>\n<p>He has the ability. He has the confidence. He even has the stamina. He just doesn't have the desire. And, I suspect, he wanted to piss his father off.</p>\n<p>Good thing the carpet is 100% polyester.</p>","fields":{"title":"Old Enough To Know Better","date":"29 Apr 2003","url":"https://papascott.de/2003-04-29-old-enough-to-know-better/"}}},"pathContext":{"slug":"/2003-04-29-old-enough-to-know-better/","prev":{"fields":{"title":"Getting Entropy","date":"29 Apr 2003","slug":"/2003-04-29-getting-entropy/"}},"next":{"fields":{"title":"Quick Links","date":"29 Apr 2003","slug":"/2003-04-29-quick-links/"}}}}

